import React from 'react';
import PropTypes from 'prop-types';

const Header = ({message}) =>{
	return (
		<h2 className="text-center">
			{message  + 's'}
		</h2>
	);
};

Header.propTypes = {
	message: PropTypes.string
}

Header.defaultProps = {
	message: 'Contentful Testing Site'
}

export default Header;


